/* Basic Reset */
*,
*::before,
*::after {
  box-sizing: border-box;
}

body {
  font-family: System, monospace;
  font-style: bold;
  background-color: #00a7a7; 
  color: black;
}

.header {
    display: flex; 
    align-items: center; 
    justify-content: start; 
    padding: 10px; 
  }
  
  .profile-picture img {
    max-width: 250px; /* Set a max size for larger screens */
    height: auto; /* Maintain aspect ratio */
    object-fit: cover;
  }
  
  .profile-info {
    margin-left: 20px; /* Adds spacing between image and text */
  }
  
  /* Responsive adjustments */
  @media (max-width: 768px) {
    .window {
      flex-direction: column;
      text-align: left;
      align-items: center; /* Center-align items for smaller screens */
    }
  
    .profile-picture img {
      max-width: 150px; /* Smaller size for smaller screens */
      margin-bottom: 10px; /* Space below the image */
    }
  
    .profile-info {
      margin-left: 0; /* No need for left margin in column layout */
    }
  }

  .window .about-me, .window .about-me h2, .window .goals-section h2 {
    text-align: center;
  }
  
  .window .about-me p, .window .goals-section > div {
    text-align: left;
    max-width: 80%; /* Adjust based on your design preference */
    margin: auto; /* This centers the text blocks if they're less than 100% width */
  } 

  .window .goals-section > div {
    text-align: left; /* Ensure the goals details are aligned to the left, adjust as needed */
  }


.top-menu-bar {
    top: 0;
    left: 0;
    right: 0;
    font-family: System, monospace;
    font-style: bold;
    background: silver;
    font-weight: bolder;
    text-align: center;
}

/* Classic Windows 95 window */
.window {
    display: flex;
    align-items: center;
    position: relative;
    cursor: default;
    margin: 2em auto;
    width: 80%;
    background: silver;
    color: #181818;
    border: 6px double #fff;
    padding: 1em;
  box-shadow: 0 0 0 3px #909092, 1em 1em 3px 0 rgba(0,0,0,.5);
}

.window-titlebar {
  background-color: #000080; /* Dark blue */
  color: white;
  padding: 4px;
}

/* 3D Button */
.button {
  background-color: #c0c0c0;
  border-top: 2px solid #ffffff; /* White top border for 3D effect */
  border-left: 2px solid #ffffff;
  border-bottom: 2px solid #808080; /* Dark border bottom for 3D effect */
  border-right: 2px solid #808080;
  padding: 5px 15px;
  font-family: 'MS Sans Serif', Arial;
  cursor: pointer;
}

.button:active {
  border-top: 2px solid #808080; /* Invert for pressed effect */
  border-left: 2px solid #808080;
  border-bottom: 2px solid #ffffff;
  border-right: 2px solid #ffffff;
}

@keyframes expand {
  from {
    transform: scaleY(0);
    opacity: 0;
  }
  to {
    transform: scaleY(1);
    opacity: 1;
  }
}

@keyframes minimize {
  from {
    transform: scaleY(1);
    opacity: 1;
  }
  to {
    transform: scaleY(0);
    opacity: 0;
  }
}

.retro-window {
  transform-origin: top;
  overflow: hidden;
  display: flex;
  flex-direction: column;
  background: #c0c0c0; /* Retro grey */
  border: 2px solid #000; /* Retro black border */
  padding: 20px;
  width: 80%;
  margin: 20px auto;
  box-shadow: 5px 5px 0px #000;
}

.expand {
  animation: expand 0.5s forwards;
}

.minimize {
  animation: minimize 0.5s forwards;
}